.create-room {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}
.create-room-modal {
  position: relative;
  background: white;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  padding-bottom: 30px;
}
.create-room-modal input {
  margin-right: 10px;
}
.create-room-error {
  position: absolute;
  width: 100%;
  bottom: 5px;
  left: 5px;
  color: red;
}
