.room-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.room-users {
  height: 150px;
  overflow-y: auto;
  width: 250px;
  border: 1px solid black;
  text-align: center;
  margin-top: 5px;
}
.room-users li {
  padding: 2px;
}
.room-buttons {
  display: flex;
  justify-content: space-between;
  width: 250px;
  margin-top: 5px;
}
