.login-screen {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-screen > * {
  margin-right: 5px;
}

.login-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}
.login-error {
  position: absolute;
  color: red;
  left: 0;
  font-size: 12px;
  top: 100%;
}
