.rooms-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.rooms-list {
  height: 150px;
  overflow-y: auto;
  width: 250px;
  border: 1px solid black;
  margin-top: 5px;
}
.rooms-item {
  padding: 3px;
}

.rooms-item:hover {
  background: burlywood;
  cursor: pointer;
}
.rooms-item.selected {
  background: rgb(165, 127, 76);
}
.rooms-buttons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 250px;
  margin-top: 10px;
}
