.card {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 20px;
  background: rgb(237, 228, 231);
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  cursor: pointer;
  backface-visibility: hidden;
  animation: 0.7s show ease-in-out;
  transform: rotateX(360deg) scale(1);
}

@keyframes show {
  from {
    transform: rotateX(180deg) scale(0.4);
  }
  to {
    transform: rotateX(360deg) scale(1);
  }
}

.card.red {
  color: red;
  background-color: rgb(252, 153, 153);
}

.card.blue {
  color: blue;
  background-color: rgb(180, 180, 255);
}

.card.black {
  background-color: rgb(55, 55, 55);
}

.card.neutral {
  color: saddlebrown;
  background: rgb(237, 228, 231);
}
.card.opened {
  opacity: 0.1;
  transform: rotateX(0deg);
  transition: transform 1s ease-in-out, background 0.5s linear 0.5s,
    color 0.5s linear 0.5s, opacity 0.3s linear 0.95s;
}

.card-value {
  position: relative;
  text-align: center;
}
.card-value::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: black;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%) scaleX(0);
  transform-origin: 50%;
}
.card.opened .card-value::before {
  transform: translateX(-50%) translateY(-50%) scaleX(1);
  transition: transform 0.3s ease-in-out 0.95s;
}
@media screen and (max-width: 650px) {
  .card {
    font-size: 10px;
    letter-spacing: 0px;
  }
}
