.App {
  background-color: rgb(145, 112, 142);
}
.app-button {
  background: linear-gradient(273deg, #d5d5d5 0%, #aaaaaa 100%);
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  transition: background 1s linear;
}
.app-button:hover {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2) inset;
  cursor: pointer;
}

.app-input {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid black;
}
.app-button:disabled {
  opacity: 0.5;
}
.app-button:disabled:hover {
  box-shadow: none;
  cursor: initial;
}
