.game {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 1100px;
  margin: 0 auto;
}
.refresh {
  position: absolute;
  top: 10px;
  left: 10px;
}
.game-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
.card-area {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 110px);
  gap: 5px;
}
.team-wrapper {
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media screen and (max-width: 650px) {
  .game {
    width: 100%;
    max-width: none;
  }
  .card-area {
    gap: 1px;
    padding: 1px;
    grid-template-rows: repeat(5, 90px);
  }
}
