.score-wrapper {
  display: flex;
  align-items: center;
}
.progress-wrapper {
  border: 2px solid rgb(51, 50, 50);
  border-radius: 5px;
  height: 20px;
  margin: 0 9px;
  position: relative;
  width: 300px;
}
.progress-wrapper div {
  height: 100%;
  position: absolute;
  top: 0;
  border-radius: 3px;
  border: 2px solid transparent;
}
.progress-wrapper div:first-child {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.progress-wrapper div:last-child {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
.progress-wrapper div.all-border {
  border-radius: 3px;
}
.score-value {
  font-weight: 700;
  font-size: 20px;
}

@media screen and (max-width: 650px) {
  .progress-wrapper {
    width: 150px;
  }
}
